body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  height: 100vh;
  display:flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App-header {
  display: flex;
  align-content: center;
  justify-content: center;
  color: white;
  -webkit-animation: fade 7s linear 2s;
          animation: fade 7s linear 2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  padding: 20px;
}

@-webkit-keyframes fade {
  0%{
    opacity: 1;
    visibility: visible;
  }
  50%{
    opacity: 0;
    visibility: hidden;
  }
  100%{
    opacity: 1;
    visibility: visible;
    align-self: flex-start;
  }
}

@keyframes fade {
  0%{
    opacity: 1;
    visibility: visible;
  }
  50%{
    opacity: 0;
    visibility: hidden;
  }
  100%{
    opacity: 1;
    visibility: visible;
    align-self: flex-start;
  }
}

.title{
  font-size: 5rem;
}

#search-q{
    display: inline-flex;
    width: 90%;
    border-radius: 2px 0 0 2px;
}

#search-form{
    width: 100%;
}

#submit{
    cursor: pointer;
    width: 65px;
    border-radius: 0 2px 2px 0 !important;
    margin: 0 0 4px 0;
}

.container{
    /* width: 0; */
    display: flex;
    visibility: hidden;
    align-items: center;
    justify-content: center;
    -webkit-animation: appear 4s ease-in 10s;
            animation: appear 4s ease-in 10s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

@-webkit-keyframes appear{
    0%{
        width: 80%;
        /* visibility: visible; */
        opacity: 0.01;
    }
    100%{
        visibility: visible;
        width: 80%;
        opacity: 1;
    }
}

@keyframes appear{
    0%{
        width: 80%;
        /* visibility: visible; */
        opacity: 0.01;
    }
    100%{
        visibility: visible;
        width: 80%;
        opacity: 1;
    }
}
